import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import styles from './SpiritsScrTrigger.module.scss';

gsap.registerPlugin(ScrollTrigger);

const SpiritsScrTrigger = () => {
  const triggerRef = useRef(null);
  const ulRef = useRef(null);

  useEffect(() => {
    const ulWidth = ulRef.current.scrollWidth;
    const containerWidth = triggerRef.current.offsetWidth;

    gsap.to(ulRef.current, {
      x: -ulWidth + containerWidth, // ul    
      ease: "power1.out",
      scrollTrigger: {
        trigger: triggerRef.current,
        start: 'center bottom', //      
        end: 'center top', //      
        scrub: true,
        // pin: true, //  
        anticipatePin: 1, //        
      },
    });

    //   
    gsap.to(triggerRef.current, {
      backgroundColor: '#404040', //   
      scrollTrigger: {
        trigger: triggerRef.current,
        start: 'bottom center', //      
        end: 'top top', //      
        scrub: 1,
        // pin: true, //  
        anticipatePin: 1, //        
      },
    });

    // Cleanup function to kill ScrollTrigger instances
    return () => {
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
    };
  }, []);

  return (
    <div className={styles.triggerWrap} ref={triggerRef}>
      <ul ref={ulRef}>
        {/* <li>Sensitive</li>
        <li>Publishing</li> */}
        <li>Communicating</li>
        <li>FrontEnd</li>
        <li>Cooperative</li>
        <li>BackEnd</li>
      </ul>
    </div>
  );
};

export default SpiritsScrTrigger;
